<template>
  <div class="container" v-if="trackInfo && trackInfo.newNode" @click="toTrackPage">
    <div class="inner">
   <div class="left">
      <span class="tracking-logo"></span>
   </div>
    <div class="content" v-if="trackInfo.pageUrlStatus === 1">
      <template v-if="trackInfo.newNode">
        <span class="info">{{trackInfo.newNode.nodeContent}}</span>
        <span class="time">{{trackInfo.newNode.nodeTime}}</span>
      </template>
    </div>
    <div class="content" v-else>
      <span class="info">{{"您的订单已拆成 "+trackInfo.packageNum+" 个包裹发出，点击‘物流详情’可查看详情。"}}</span>
    </div>
    </div>

    <div class="right">
      <span class="more"></span>
   </div>
  </div>
</template>

<script>

/*
 * 订单详情 最新物流信息
*/
export default {
  name: 'orderTracking',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
    trackInfo: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  data() {
    return {
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    /**
     * @description 跳转物流跟踪页或者物流追踪信息列表
    */
    toTrackPage() {
      if (this.parentData.resData.orderStatusName === '已付款待分享') { // 拼团未成功时不跳转物流
        return;
      }
      switch (this.trackInfo.pageUrlStatus) {
        case 1:
          // 1 跳转物流追踪页
          // 区分跳转地图页还是物流跟踪页逻辑  b2b2c并且 自营或者oco的 跳地图页，其他情况以及待确定/已取消状态的订单跳旧物流跟踪页
          if (
            this.parentData.resData.orderStatus !== 202 &&
              this.parentData.resData.orderStatus !== 200 &&
              this.parentData.resData.shippingMode === 'B2B2C' &&
              (this.parentData.resData.stockType === 'WA' || this.parentData.resData.stockType === 'OCO')) {
            this.$router.push({
              path: `/mapTrackingOrders?cOrderSn=${this.parentData.resData.cOrderSn}`
            });
          } else {
            this.$router.push({
              path: `/trackingOrders?cOrderSn=${this.parentData.resData.cOrderSn}`
            });
          }
          // this.$router.push({
          //   path: `/trackingOrders?cOrderSn=${this.parentData.resData.cOrderSn}`
          // });
          break;
        case 2:
          // 2 中间页
          this.$router.push({
            path: `/trackingDetail?orderSn=${this.parentData.resData.orderSn}&cOrderSn=${this.parentData.resData.cOrderSn}&from=detail`
          });
          break;
      }
    },
  },
}
</script>

