<template>
  <div class="content">
    <div class="block">
      <div class="line">
        <div>
          订单编号：
        </div>
        <div>
          {{parentData.resData.orderSn || ''}}<span @click="copy" class="copy">复制</span>
        </div>
      </div>
      <div class="line">
        <div>
          支付方式：
        </div>
        <div>
          {{parentData.resData.paymentName || ''}}
        </div>
      </div>
      <div class="line">
        <div>
          下单时间：
        </div>
        <div>
          {{parentData.resData.createTime || ''}}
        </div>
      </div>
      <template v-if="expandStatus">
          <div class="line" v-if="parentData.resData.payTime">
            <div>
              付款时间：
            </div>
            <div>
              {{parentData.resData.payTime || ''}}
            </div>
          </div>
          <div class="line" v-if="parentData.resData.expectedDateStr">
            <div>
              预计送达时间：
            </div>
            <div>
              {{parentData.resData.expectedDateStr || ''}}
            </div>
          </div>
          <div class="line" v-if="parentData.resData.expectedStockDateStr">
            <div>
              预计到货时间：
            </div>
            <div>
              {{parentData.resData.expectedStockDateStr || ''}}
            </div>
          </div>
          <div class="line" v-if="parentData.resData.reservationDateStr">
            <div>
              用户预约时间：
            </div>
            <div>
              {{parentData.resData.reservationDateStr || ''}}
            </div>
          </div>
          <div class="line" v-if="parentData.resData.lessShipTime">
            <div>
              发货时间：
            </div>
            <div>
              {{parentData.resData.lessShipTime | formatDate}}
            </div>
          </div>
          <div class="line" v-if="parentData.resData.expressName">
            <div>
              快递方式：
            </div>
            <div>
              {{parentData.resData.expressName || ''}}
            </div>
          </div>
          <div class="line" v-if="parentData.resData.invoiceNumber">
            <div>
              运单编号：
            </div>
            <div>
              {{parentData.resData.invoiceNumber || ''}}
            </div>
          </div>
          <div class="line" v-if="parentData.resData.receiptOrRejectTime">
            <div>
              {{parentData.resData.orderStatusName === '已取消' ? '取消时间：' : '签收时间：'}}
            </div>
            <div>
              {{parentData.resData.receiptOrRejectTime || ''}}
            </div>
          </div>
      </template>
      <div class="expand-container" v-if="showExpandBtn">
        <div class="expand-btn" @click="toggleExpand">
          <span>{{expandStatus?'收起':'展开'}}</span>
          <span class="expand-icon" :class="{'shouqi':expandStatus,'zhankai':!expandStatus}"></span>
        </div>
      </div>
    </div>

    <!-- <div @click="copy" class="copy border">
      复制
    </div> -->
  </div>
</template>

<script>
import ClipboardJS from 'clipboard'; // 复制插件

/*
 * 订单详情 订单信息
*/
export default {
  name: 'orderInfo',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  data() {
    return {
      expandStatus: false, // false 折叠 true 展开
    }
  },
  computed: {
    /**
     * @description 是否展示折叠按钮 （显示项超过3个才展示）
    */
    showExpandBtn () {
      return !!(this.parentData.resData.payTime ||
              this.parentData.resData.expectedDateStr ||
              this.parentData.resData.expectedStockDateStr ||
              this.parentData.resData.reservationDateStr ||
              this.parentData.resData.lessShipTime ||
              this.parentData.resData.expressName ||
              this.parentData.resData.invoiceNumber ||
              this.parentData.resData.receiptOrRejectTime)
    },
  },

  mounted() {
  },
  methods: {
    toggleExpand() {
      this.expandStatus = !this.expandStatus
    },
    /**
     * @description  复制订单编号
     * @param {copyData} 要复制的订单编号
     * 返回复制提示信息
    */
    copy () {
      let orderSn = this.parentData.resData.orderSn;
      const clipboard = new ClipboardJS('.copy', {
        text: function(trigger) {
          return orderSn
        }
      });

      clipboard.on('success', () => {
        this.$toast.success('复制成功');
      });

      clipboard.on('error', () => {
        this.$toast.success('复制失败');
      });
    }
  },
}
</script>

