      <status-icon :status="svgStatus"></status-icon>
      <span class="status-text">
        <span v-if="parentData.resData.orderStatusName === '待付款'">等待付款</span>
        <span v-if="parentData.resData.orderStatusName === '退款中'">退款中</span>
        <span v-if="parentData.resData.orderStatusName === '已取消'">已取消</span>
        <span v-if="parentData.resData.orderStatusName === '待确认'">待确认</span>
        <span v-if="parentData.resData.orderStatusName === '已评价'">已评价</span>
        <span v-if="parentData.resData.orderStatusName === '已确认'">已确认 等待收货</span>
        <span v-if="parentData.resData.orderStatusName === '交易完成'">交易完成</span>
        <span v-if="parentData.resData.orderStatusName === '订单关闭'">订单关闭</span>
        <span v-if="parentData.resData.orderStatusName === '已付款待分享'">已支付，待分享</span>
