      </span>
      </div>
      <div class="tip">
        <span v-if="parentData.resData.orderStatusName === '待付款' && parentData.resData.displayPriceFlag">
          <span>需付款：</span>
          <span class="price" v-if="parentData.resData.poiName && parentData.resData.orderType>=30 && parentData.resData.orderType<=50">
            <span class="price-int">{{parentData.resData.totalAmountS |formatPrice | formatPriceInt}}</span>
            <span class="price-float">{{parentData.resData.totalAmountS |formatPrice | formatPriceFloat}}</span>
            <span>{{parentData.resData.poiName}}</span>
          </span>
          <span class="price" v-else>
            <span class="yuan">￥</span>
            <span class="price-int">{{parentData.resData.totalAmountS |formatPrice | formatPriceInt}}</span>
            <span class="price-float">{{parentData.resData.totalAmountS |formatPrice | formatPriceFloat}}</span>
          </span>
        </span>
        <span v-if="parentData.resData.orderStatusName === '已付款待分享'">支付成功，等待分享拼团</span>
