        <span v-if="parentData.resData.orderStatusName === '订单关闭'">您的订单已关闭</span>
      </div>
      <div class="btn-container" v-if="parentData.resData.immediatePay">
        <div class="btn" @click="payOrders">立即支付</div>
      </div>
    </div>
  </div>
</template>

<script>
import StatusIcon from './statusIcon.vue'
const statusMap = {
  '部分缺货': 1,
  '待付款': 2,
  '退款中': 3,
  '已取消': 4,
  '待确认': 5,
  '已评价': 6,
  '已确认': 7,
  '交易完成': 8,
  '订单关闭': 9,
  '已付款待分享': 10,
}
/*
 * 订单详情 订单状态
*/
export default {
  name: 'orderState',
  components: {
    StatusIcon,
  },
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  computed: {
    svgStatus() {
      return statusMap[this.parentData.resData.orderStatusName] || 0
    }
  },
  filters: {
    formatSaleNumber(v) {
      const nv = Number(v);
      const nvn = (nv / 10000).toFixed(1) + '万'
      return nv <= 9999 ? nv : nvn
    },
    formatPriceInt(v) {
      return v.split('.')[0] + '.';
    },
    formatPriceFloat(v) {
      return v.split('.')[1];
    },
  },
  data() {
    return {
    }
  },
  mounted() {
  },
  methods: {
    /**
     * @description  去支付
     *
     * @param {orderSn} 网单号
    */
    payOrders() {
      this.$router.push(`/orderCashier/${this.parentData.resData.orderSn}`);
    },
  },
}
</script>

