<template>
  <div class="content">
    <div class="header">
      <img class="right-icon" src="data:image/png;base64,">
      <span class="title">权益</span>
    </div>
    <div class="main">
      <div  v-if="!noData">
        <div v-for="(item, index) in purchaseList" :key="index" class="main-items">
          <div class="item-header" @click="handleDrawer(item)">
            <p class="items-name">{{item.rightsName}}</p>
            <img class="img" :class="item.showDetil ? 'yes' : 'no'" :src="`${$cdnImageUrl}/orders/arrow-up.png`"/>
          </div>
          <div class="items-info" v-show="item.showDetil">
            <div class="info-box">
              <span class="box-title">已选权益：</span>
              <span class="box-text">{{item.giftName}}</span>
            </div>
            <div class="info-box">
              <span class="box-title">权益类型：</span>
              <span class="box-text">{{item.giftType | giftTypeFilter}}</span>
            </div>
            <div class="info-box">
              <span class="box-title">获取方式：</span>
              <span class="box-text">{{item.rightsType | rightsTypeFilter}}</span>
            </div>
            <div class="info-box">
              <span class="box-title">兑换有效期：</span>
              <span class="box-text">{{item.exchangeStartTime | timeFilter}} 至 {{item.exchangeEndTime | timeFilter}}</span>
            </div>
            <div class="info-box">
              <span class="box-title">权益状态：</span>
              <span class="box-text">{{item.status | statusFilter}}</span>
            </div>
            <div class="info-box">
              <span class="box-title">权益规则：</span>
              <span class="box-text">{{item.activationName}}</span>
            </div>
            <div class="info-box">
              <span class="box-title">权益数量：</span>
              <span class="box-text">1</span>
            </div>
            <div class="info-box">
              <span class="box-title">权益物流信息：</span>
              <span class="box-text">{{item.logisticsInfo | logisticsInfoFilter}}</span>
            </div>
          </div>
        </div>
      </div>
      <div v-else class="no-right">
        <img class="img" src="data:image/png;base64,">
        <span class="text">权益查询失败，请稍后重试！</span>
      </div>
    </div>
  </div>
</template>
<script>
import utils from '@/utils/utils'
export default {
  name: 'purchaseRight',
  props: {
    /**
     * 父组件传入数据源
     */
    purchaseList: {
      type: Array,
      default: function () {
        return []
      },
    },
    noData: {
      type: Boolean,
      default: false
    }
  },
  filters: {
    rightsTypeFilter(val) {
      switch (val) {
        case 'single':
          return '单品'
        case 'sets':
          return '套购'
        default:
          return ''
      }
    },
    giftTypeFilter(val) {
      switch (val) {
        case 'jfmall':
          return '海贝积分'
        case 'casarte':
          return '卡萨帝积分'
        case 'entity':
          return '实物礼品'
        case 'virtual':
          return '虚拟礼品'
        default:
          return ''
      }
    },
    statusFilter(val) {
      switch (val) {
        case 1:
          return '权益在途'
        case 2:
          return '权益已激活'
        case 3:
          return '权益已领取'
        case 4:
          return '权益已过期 '
        default:
          return ''
      }
    },
    logisticsInfoFilter(val) {
      if (val) {
        let _location = this.$util.strJSONParse(val, {})
        return `${_location.logisticsName || ''} ${_location.shipmentNo || ''}`
      } else {
        return ''
      }
    },
    timeFilter(val) {
      return utils.formatTime(Number(val), 'Y-M-D')
    }
  },
  data() {
    return {

    }
  },
  methods: {
    /**
     * @description 点击查看详细信息
     */
    handleDrawer(item) {
      item.showDetil = !item.showDetil
    },
  }
}
</script>
