<template>
  <div class="content">
    <div class="title" v-if="parentData.resData.o2oStoreName" @click="toShopDetail(parentData.resData.o2oStoreId)">
      <span class="shop-logo"></span>{{parentData.resData.o2oStoreName || ''}}<span class="shop-arrow"></span>
    </div>
    <div class="detail" @click="viewCommodityDetail(parentData.resData.productId)">
      <img class="thumbnail" :src="parentData.resData.defaultImageUrl | imageToHttps" alt="商品封面图"/>
      <div class="text">
        <div class="secondary">
         <div class="p-name">{{(parentData.resData.productFullName || '') + parentData.resData.productName || '' | formatTitleSpace}}</div>
         <!-- <div class="model">
          {{parentData.resData.productName || ''}}
         </div> -->
         <div class="specifications" v-if="parentData.resData.attrName">
          {{parentData.resData.attrName || ''}}
        </div>
        </div>

        <!-- <div class="model" :style="{'margin-bottom': parentData.resData.attrName ? '0.04rem' : '0.25rem'}">
          {{parentData.resData.productName || ''}}
        </div> -->
        <!-- <div class="specifications" v-if="parentData.resData.attrName">
          {{parentData.resData.number? '数量：'+parentData.resData.number :''}} {{parentData.resData.attrName || ''}}
        </div> -->
        <div class="payment">
          <template v-if="parentData.resData && parentData.resData.displayPriceFlag">
          <div class="money" v-if="parentData.resData.poiName && parentData.resData.orderType>=30 && parentData.resData.orderType<=50">
            <div>
              {{parentData.resData.price}}{{parentData.resData.poiName}}
            </div>
          </div>
          <div class="money" v-else>
            <div class="yuan">
              ￥
            </div>
            <div>
              <span class="price-int">{{parentData.resData.price |formatPrice | formatPriceInt}}</span>
              <span class="price-float">{{parentData.resData.price |formatPrice | formatPriceFloat}}</span>
            </div>
          </div>
          </template>
          <div class="number">
            <div>
              x
            </div>
            <div>
              {{parentData.resData.number || ''}}
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="repire-btn-container" v-if="parentData.resData.orderRepairs && (parentData.resData.stockType === 'STORE' || parentData.resData.stockType === 'O2O' || parentData.resData.stockType === 'SYN')">
      <div class="repire-btn" @click="toRepair">退款详情</div>
    </div>
    <div class="customer-service">
      <!-- <div class="column" @click="customerService">
        <span class="icon-customer-service"></span>
        <div class="label">
          联系客服
        </div>
      </div> -->
      <div class="column">
        <a :href="`tel:${parentData.resData.phoneNumber || '4006999999'}`">
