<script>
/**
 * 方法配置文件
 */
// import util from '@/utils/utils';

/*
 * 订单详情 商品详情
*/
export default {
  name: 'commodityDetails',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  filters: {
    formatPriceInt(v) {
      return v.split('.')[0] + '.';
    },
    formatPriceFloat(v) {
      return v.split('.')[1];
    },
    formatTitleSpace(v) {
      return v.replace(/ /g, '');
    },
  },
  data() {
    return {

    }
  },
  mounted() {
  },
  methods: {
    /**
     * @description  点击查看商品详情
    */
    viewCommodityDetail(productId) {
      const context = this;
      if (context.parentData.resData.canJumpProduct) {
        context.$router.push({
          path: `/goodsDetail?productId=${productId}`,
        });
      }
    },

    /**
     * @description  点击跳转到店铺
    */
    toShopDetail(storeId) {
      const context = this;
      context.$router.push({
        path: `/shopDetail?storeId=${storeId}`,
      });
    },
    /**
     * 跳转退款详情
    */
    toRepair() {
      this.$router.push({ path: '/details', query: { orderProductId: this.parentData.resData.orderProductId } }) // 退货详情回传网单id
    }

    // /**
    //  * @description  点击跳转到客服web div页面
    // */
    // customerService() {
    //   const context = this;
    //   util.openNewWindow(`/v3/h5/sg/common/customService.html?orderid=${context.parentData.resData.orderId}&flag=${context.$store.state.token}`)
    // }
  },
}
</script>

