<template>
  <div class="content"  @click="operationClick">
    <div class="customer-service" @click="customerService">
      <span class="icon-customer-service1"></span>
      <div>
        客服
      </div>
    </div>

    <div class="block">
      <!-- canRepair是否可退货  canSubmitRepaier是否可提交退货-->
      <div class="more-container" v-if="parentData.resData.relationPaymentStatus !== 100 && !!parentData.resData.cPaymentStatus && parentData.resData.cPaymentStatus !== 200 && btnCount > 1">
        <span class="text" @click="toolTipToggle">更多</span>
        <div :class="{'tooltip':true,'single-btn':parentData.resData.cPaymentStatus !== 200 && (!parentData.resData.canRepair || btnCount <= 2)}" v-show="tooltipShow">
          <div class="tooltip-content">
            <ul class="tooltip-btn-container">
              <li class="tooltip-btn" v-if="parentData.resData.canRepair&& btnCount > 2" @click="returnedGoods">退款/退货</li>
              <li class="tooltip-btn" v-if="parentData.resData.canInvoice" @click="toInvoice()">发票服务</li>
            </ul>
          </div>
          <div class="arrow">
          </div>
        </div>
      </div>
      <div v-if="parentData.resData.canInvoice && (btnCount === 1 || btnCount === 0)" @click="toInvoice" class="returned-goods">
      <!-- <div v-if="!parentData.resData.paidToShare &&
      (parentData.resData.relationPaymentStatus !== 100 && !!parentData.resData.cPaymentStatus && parentData.resData.cPaymentStatus !== 200 && btnCount === 0)||
      (parentData.resData.relationPaymentStatus !== 100 && !!parentData.resData.cPaymentStatus && parentData.resData.cPaymentStatus !== 200 && btnCount === 1)" @click="toInvoice" class="returned-goods"> -->
      <!-- <div v-if="(!parentData.resData.payRelationOrder && !!parentData.resData.cPaymentStatus && parentData.resData.cPaymentStatus !== 200 && btnCount === 0)||(!parentData.resData.payRelationOrder && !!parentData.resData.cPaymentStatus && parentData.resData.cPaymentStatus !== 200 && btnCount === 1)" @click="toInvoice" class="returned-goods"> -->
        发票服务
      </div>
      <div v-if="parentData.resData.canRepair && btnCount <= 2" @click="returnedGoods" class="returned-goods">
        退款/退货
      </div>
      <div v-if="parentData.resData.canTrack" @click="toTrackPage" class="tracking-orders">
        查看物流
      </div>
      <div v-if="parentData.resData.immediatePay" @click="payOrders" class="tracking-orders to-pay">
        立即支付
      </div>
      <div v-if="serviceScheduleBtn" class="view-evaluation" @click="goOtherUrl('ssUrl')">服务进度</div>
      <div v-if="checkCardBtn" class="tracking-orders" @click="goOtherUrl('ccUrl')">查看卡号</div>
      <div v-if="parentData.resData.canClickConfirm" @click="confirmReceipt" class="confirm-receipt">
        确认收货
      </div>
      <div v-if="parentData.resData.assessed" @click="viewEvaluation" class="view-evaluation">
        查看评价
      </div>
      <div v-if="parentData.resData.canAssess" @click="toEvaluate" class="to-evaluate">
        去评价
      </div>
      <div v-if="!!parentData.resData.paidToShare"
        @click="shareOrder"
        class="copy"
        style="border-color: #2878FF; border-radius: .32rem; color: #2878FF;">
        邀请拼团
      </div>
    </div>
  </div>
</template>

<script>
import { getUcLoginUrl } from '@/http/service/common.js'
import userTarget from '@/utils/userTarget.js'
/*
 * 接口配置文件
 * 方法配置文件
*/
import personalApi from '@/http/service/index';
// import ClipboardJS from 'clipboard'; // 复制插件
// import util from '@/utils/utils';
// import md5 from '@/utils/md5'

/**
 * tost提醒
 */
import Vue from 'vue';
import { Toast } from 'vant';

Vue.use(Toast);

/*
 * 订单详情 订单其他操作
*/
export default {
  name: 'orderOperation',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
    trackInfo: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  data() {
    return {
      tooltipShow: false,
      btnCount: 0, // 当前页面操作按钮数量
    }
  },
  computed: {
    serviceScheduleBtn() {
      return this.parentData.resData && this.parentData.resData.businessFunctionVO && this.parentData.resData.businessFunctionVO.serviceSchedule;
    },
    checkCardBtn() {
      return this.parentData.resData && this.parentData.resData.businessFunctionVO && this.parentData.resData.businessFunctionVO.checkCard;
    }
  },
  mounted() {
    this.getBtnCount()
  },
  methods: {
    /**
     * 免登到第三方
     */
    goOtherUrl(page) {
      // 目前只是智家
      if (this.$util.isHaierApp()) {
        this.$util.openNewWindow(this.parentData.resData.businessFunctionVO[page], true, true, false)
      } else {
        getUcLoginUrl({
          redirectUri: this.parentData.resData.businessFunctionVO[page],
          target: userTarget[`${page}Target`],
          targetTest: userTarget[`${page}Target_test`],
          redirectUriTest: this.parentData.resData.businessFunctionVO[page],
        }).then(({ data }) => {
          console.log(data.data)
          if (data.success) {
            location.href = data.data
          } else {
            this.$toast(data.message)
          }
        })
      }
    },

    /**
     * @description  发票服务跳转逻辑
    */
    toInvoice() {
      this.$util.gioTrackWithVariable('mall_order_invoice', {
        order_id: this.parentData.resData.orderSn ? this.parentData.resData.orderSn + '' : 'null',
      }).then(() => {
        let url = '' // 跳转页面地址
        let context = this
        switch (this.parentData.resData.invoicePageType) {
          case 0:
            // 0 未开票状态 跳转网单开票页
            url = '/orderInvoice'
            break;
          case 1:
            // 1 已开票状态 跳转发票列表页
            url = '/orderInvoiceList'
            break;
          case 2:
            // 2 订单超时状态 跳转发票超时页
            url = '/invoiceOverTime'
            break;
          case 3:
            // 3 订单逆向状态 跳转发票逆向页
            url = '/invoiceReverse'
            break;
          case 4:
            // 4 食联网无发票订单 跳转发票信息缺省页
            url = '/invoiceNodata'
            break;
        }
        context.$router.push({
          path: url,
          query: {
            orderNo: context.parentData.resData.cOrderSn
          }
        });
      })
    },
    /**
     * @description  统计当前操作按钮的数量
    */
    getBtnCount() {
      let count = 0
      if (this.parentData.resData.canTrack) {
        count++
      }
      if (this.parentData.resData.canClickConfirm) {
        count++
      }
      if (this.parentData.resData.assessed) {
        count++
      }
      if (this.parentData.resData.canAssess) {
        count++
      }
      if (this.parentData.resData.canRepair) {
        count++
      }
      this.btnCount = count
    },
    operationClick(event) {
      if (event.target.className !== 'tooltip' && event.target.className !== 'tooltip-content' && event.target.className !== 'tooltip-btn-container' && event.target.className !== 'tooltip-btn' && event.target.className !== 'more-container' && event.target.className !== 'text') {
        this.tooltipShow = false
      }
    },
    /**
     * @description  更多按钮折叠控制
    */
    toolTipToggle() {
      this.tooltipShow = !this.tooltipShow
    },
    /**
     * @description  退货、换货
     * @param {canSubmitRepaier} true可以点击按钮 false不能点击按钮
    */
    returnedGoods () {
      const context = this;
      if (context.parentData.resData.canSubmitRepaier) {
        context.$router.push({
          path: `/refund`,
          query: {
            cOrderSn: context.parentData.resData.cOrderSn,
            orderProductId: context.parentData.resData.orderProductId,
          }
        });
      } else {
        Toast.fail('暂时无法退货、换货');
      }
    },

    /**
     * @description  确认收货
     * @param {canClickConfirm} true可以点击按钮 false不能点击按钮
    */
    confirmReceipt () {
      const context = this;
      if (context.parentData.resData.canClickConfirm) {
        const data = {
          cOrderSn: context.parentData.resData.cOrderSn,
        }
        personalApi.confirmReceive(data).then(res => {
          const _resData = res.data;
          if (_resData.success) {
            // MB13420 商城订单-订单签收成功
            this.$util.gioTrackWithVariable('MB13420', {
              user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
              member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
              order_id: context.parentData.resData.orderSn ? context.parentData.resData.orderSn + '' : 'null',
              par_value: context.parentData.resData.totalAmount ? context.parentData.resData.totalAmount + '' : 'null',
              productId_var: context.parentData.resData.productId ? context.parentData.resData.productId + '' : 'null'
            })
            Toast.success('确认收货成功');
            /**
             * @description 订单详情
            */
            context.$parent.getDetail();
          } else {
            Toast.fail(_resData.message);
          }
        })
      } else {
        Toast.fail('暂时无法确认收货');
      }
    },

    /**
     * @description  追踪订单
     * @param {canClickConfirm} true可以点击按钮 false不能点击按钮
     * @param {orderSn} 网单号
    */
    trackingOrders () {
      const context = this;
      context.$router.push({
        path: `/trackingOrders?orderSn=${context.parentData.resData.orderSn}&cOrderSn=${context.parentData.resData.cOrderSn}`
      });
    },
    /**
     * @description  去支付
     *
     * @param {orderSn} 网单号
    */
    payOrders() {
      this.$router.push(`/orderCashier/${this.parentData.resData.orderSn}`);
    },

    /**
     * @description  查看评价
     * @param {canClickConfirm} true可以点击按钮 false不能点击按钮
    */
    viewEvaluation () {
      const context = this;
      context.$router.push({
        path: `/viewEvaluation?cOrderSn=${context.parentData.cOrderSn}`
      });
    },

    /**
     * @description  去评价
     * @param {canClickConfirm} true可以点击按钮 false不能点击按钮
     * @param {orderSn} 网单号
    */
    toEvaluate () {
      const context = this;
      context.$router.push({
        path: `/evaluate?cOrderSn=${context.parentData.resData.cOrderSn}`
      });
    },

    /**
     * @description  点击跳转到客服web div页面
    */
    customerService() {
      // 32类型的订单客服改为电话客服
      if (this.parentData.resData.orderType === 32) {
        const telnum = '4006608386'
        this.$dialog.confirm({
          title: '立即拨打电话',
          message: telnum,
          confirmButtonText: '呼叫'
        }).then(() => {
          const a = document.createElement('a')
          a.href = `tel:${telnum}`
          a.click()
        }).catch(() => {})
        return
      }
      // 如果是海尔app隐藏客服头部栏，使用app内头部栏，否则使用客服工具头部栏
      this.$store.dispatch('goPageByType', {
        type: 29,
        params: {
          accountId: this.parentData.resData.storeCode || '',
          sceneId: 'cj093'
        }
      })
      // }
    },
    /**
     * @description 验证传入的88码是否开通商家客服
    */
    isCanGetCustomer(storeCode) {
      return new Promise((resolve, reject) => {
        const data = {
          storeCode: storeCode
        };
        personalApi.getCustomer(data).then(res => {
          if (res.data.success) {
            resolve(res.data.result)
          }
        })
      })
    },
    /**
     * @description 跳转物流跟踪页或者物流追踪信息列表
    */
    toTrackPage() {
      this.$util.gioTrackWithVariable('mall_order_viewlogistics', {
        order_id: this.parentData.resData.orderSn || 'null',
      }).then(() => {
        switch (this.trackInfo.pageUrlStatus) {
          case 1:
          // 1 跳转物流追踪页
          // 区分跳转地图页还是物流跟踪页逻辑  b2b2c并且 自营或者oco的 跳地图页，其他情况以及待确定/已取消状态的订单跳旧物流跟踪页
            if (
              this.parentData.resData.orderStatus !== 202 &&
              this.parentData.resData.orderStatus !== 200 &&
              this.parentData.resData.shippingMode === 'B2B2C' &&
              (this.parentData.resData.stockType === 'WA' || this.parentData.resData.stockType === 'OCO')) {
              this.$router.push({
                path: `/mapTrackingOrders?cOrderSn=${this.parentData.resData.cOrderSn}`
              });
            } else {
              this.$router.push({
                path: `/trackingOrders?cOrderSn=${this.parentData.resData.cOrderSn}`
              });
            }
            // this.$router.push({
            //   path: `/trackingOrders?cOrderSn=${this.parentData.resData.cOrderSn}`
            // });
            break;
          case 2:
            // 2 中间页
            this.$router.push({
              path: `/trackingDetail?orderSn=${this.parentData.resData.orderSn}&cOrderSn=${this.parentData.resData.cOrderSn}&from=detail`
            });
            break;
        }
      })
    },
    /** 拼团分享 */
    shareOrder() {
      this.$router.push(`/groupBookingDetail?orderSn=${this.parentData.resData.orderSn}`)
      // const shareUrl = `${location.origin}/sgmobile/groupBookingDetail?orderSn=${this.parentData.resData.orderSn}`;
      // if (this.$util.isHaierApp()) {
      //   this.$util
      //     .inAppShare({
      //       title: '海尔智家',
      //       desc: '海尔智家好物拼团，邀你参与',
      //       url: shareUrl,
      //     })
      // } else {
      //   const clipboard = new ClipboardJS('.copy', {
      //     text: function(trigger) {
      //       return shareUrl
      //     }
      //   });

      //   clipboard.on('success', () => {
      //     console.log('---', this)
      //     this.$toast.success('链接复制成功');
      //   });

      //   clipboard.on('error', () => {
      //     this.$toast.success('复制失败');
      //   });
      // }
    }
  },
}
</script>

