<template>
  <div class="content-overflow">
    <Header>
      <div slot="left" @click="doBack" class="backIcon"><img :src="`${$cdnImageUrl}${titleShow?'/orders/order-back-black.svg':'/common/back_white.png'}`"></div>
      <div slot="title">
        <span v-if="titleShow">订单详情</span>
      </div>
    </Header>
    <template v-if="data.resData">
      <div class="scroll-box"  @scroll="handleScroll">
        <!-- <div ref="scrollBox"></div> -->
        <order-state :parentData = "data"></order-state>
        <div class="addr-container" :style="{
          marginTop: '-0.4rem'
          }">
          <div ref="scrollBox"></div>
          <order-tracking :parentData = "data" :trackInfo="trackInfo" v-if="trackInfo"></order-tracking>
          <order-address v-if="data.resData.showReceiverInfo" :parentData = "data"></order-address>
        </div>
        <commodity-details :parentData = "data"></commodity-details>
        <remark-info :parentData = "data"/>
        <!-- <order-invoice v-if="data.resData.invoiceTitle" :parentData = "data"></order-invoice> -->
        <!-- <van-list
          v-if="hiddenRight"
          :finished="finished"
          :finished-text="finishedText"
          @load="onLoad"
          v-model="loading"
        > -->
        <purchase-right v-if="hiddenRight" :purchaseList="purchaseList" :noData="noData"></purchase-right>
        <!-- </van-list> -->
        <order-payment :parentData = "data"></order-payment>
        <order-info :parentData = "data"></order-info>
        <guess-you-like :more="true"></guess-you-like>
        <div class="bottom-area"></div>
      </div>
      <order-operation :parentData = "data" :trackInfo="trackInfo" style="position: absolute; bottom: 0; left: 0; z-index: 10;"></order-operation>
    </template>
  </div>
</template>

<script>
import guessYouLike from '@/components/guessYouLick'
import URL from '@/http/url'
import Header from '@/components/header'

import { post } from '@/http/http'
/*
 * 订单状态
*/
import orderState from './state';

/*
 * 收货地址
*/
import orderAddress from './address';

/**
 * 最新物流信息
*/
import orderTracking from './tracking';

/*
 * 商品详情
*/
import commodityDetails from './commodity-details';

/**
 * 留言信息
 */
import remarkInfo from './remarkInfo';

/*
 * 发票信息
*/
// import orderInvoice from './invoice';

/*
 * 支付信息
*/
import orderPayment from './payment';

/*
 * 权益
*/
import purchaseRight from './purchase-right';

/*
 * 订单信息
*/
import orderInfo from './order-info';

/*
 * 订单操作区域
*/
import orderOperation from './operation';

/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';

/**
 * tost提醒
 */
import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);

/*
 * 订单详情
*/
export default {
  name: 'orderDetail',
  components: {
    orderState,
    orderAddress,
    commodityDetails,
    // orderInvoice,
    orderPayment,
    orderInfo,
    orderOperation,
    purchaseRight,
    guessYouLike,
    remarkInfo,
    Header,
    orderTracking
  },
  data() {
    return {
      /**
       * {cOrderId} 网单id number
       * {cOrderSn} 网单号 string
       */
      data: {
        cOrderId: '',
        cOrderSn: '',
        resData: null,
      },
      purchaseList: [],
      finished: false,
      loading: false,
      PageIndex: 0,
      pages: 1, // 总页数
      finishedText: '没有更多啦~',
      noData: false, // 权益是否查询失败
      hiddenRight: true, // 是否隐藏权益
      trackInfo: null, // 物流信息
      rem: 0,
      titleShow: false
    }
  },
  created() {
    const context = this;
    const _query = context.$route.query;
    context.data.cOrderId = _query.cOrderId;
    context.data.cOrderSn = _query.cOrderSn;
    this.rem = this.getREM()
  },
  mounted() {
    // const context = this;
    // /**
    //  * @description  个人中心 订单详情
    //  * @param {cOrderId} 网单id number
    //  * @param {cOrderSn} 网单号 string
    //  * 返回接口请求数据
    // */
    // if (this.$util.isHaierApp()) {
    //   // U+ 登陆处理
    //   this.$store.dispatch('handleUpAuth').then(isLogin => {
    //     // 登陆正常 调用加购接口
    //     if (isLogin) {
    //       context.getDetail();
    //     } else {
    //       context.$store.dispatch('doLogin').then(res => {
    //         this.$store.dispatch('handleUpAuth').then(isLogin => {
    //           if (isLogin) {
    //             context.getDetail();
    //           } else {
    //             this.$toast('用户未登录')
    //           }
    //         })
    //       })
    //     }
    //   })
    // } else {
    //   if (this.$store.getters.getLoginStatus) {
    //     // 有paramId进入自动加购逻辑，否则获取购物车列表
    //     context.getDetail();
    //   } else {
    //     context.$store.dispatch('doLogin')
    //   }
    // }
  },
  activated() {
    const context = this;
    /**
     * @description  个人中心 订单详情
     * @param {cOrderId} 网单id number
     * @param {cOrderSn} 网单号 string
     * 返回接口请求数据
    */
    if (this.$util.isHaierApp()) {
      // U+ 登陆处理
      this.$store.dispatch('handleUpAuth').then(isLogin => {
        // 登陆正常 调用加购接口
        if (isLogin) {
          context.getDetail();
          // 加载权益
          context.onLoad()
        } else {
          context.$store.dispatch('doLogin').then(res => {
            this.$store.dispatch('handleUpAuth').then(isLogin => {
              if (isLogin) {
                context.getDetail();
                // 加载权益
                context.onLoad()
              } else {
                this.$toast('用户未登录')
              }
            })
          })
        }
      })
    } else {
      if (this.$store.getters.getLoginStatus) {
        // 有paramId进入自动加购逻辑，否则获取购物车列表
        context.getDetail();
        // 加载权益
        context.onLoad()
      } else {
        context.$store.dispatch('doLogin')
      }
    }
  },
  methods: {
    handleScroll(e) {
      // top 0.36 height 0.92 bg 3.72    -0.4 -1.36
      let _eiTop = (this.$refs.scrollBox.getBoundingClientRect().top) / this.rem
      // let topMax = 3.72 - 0.4 + 0.36 // 有支付按钮 地址栏顶部距离
      let containerMax = 3.72 - 0.4 + 0.36 - 0.92 // 有支付按钮 地址栏距头部栏底部距离
      if (!(this.data.resData.payOnline && this.data.resData.paymentStatus === 100 && this.data.resData.orderStatus === 200)) {
        // topMax = 3.72 - 1.36 + 0.36// 无支付按钮 地址栏顶部距离
        containerMax = 3.72 - 1.36 - 0.92 + 0.36 // 无支付按钮 地址栏距头部栏底部距离
      }
      // if (_eiTop >= 0.92 - 0.36 && _eiTop < topMax + 1) {
      // console.log('scrollBox:', _eiTop + '|' + (1 - (_eiTop - 0.92) / containerMax))
      let headerDom = document.querySelector('.van-nav-bar--fixed')
      headerDom.style.backgroundColor = `rgba(255,255,255,${(1 - (_eiTop - 0.92) / containerMax)})`
      if ((_eiTop) <= 0.92 + 0.36) {
        this.titleShow = true
      } else {
        this.titleShow = false
      }
      // }
    },
    getREM() {
      let rem = parseInt(window.getComputedStyle(document.documentElement, null).getPropertyValue('font-size'))
      return rem
    },
    /**
     * @description 权益上拉加载
     */
    onLoad() {
      this.PageIndex += 1
      if (this.PageIndex <= this.pages) {
        this.loading = true
        this.finished = false
        let params = {
          pageNum: this.PageIndex,
          pageSize: 50,
          orderNo: this.data.cOrderSn
        }
        post(URL.getRightList, params).then((res) => {
          if (res.success) {
            if (res.data && res.data.result && res.data.result.length) {
              // 添加属性，控制抽屉效果
              res.data.result.map(item => {
                this.$set(item, 'showDetil', false)
              })
              this.purchaseList = this.purchaseList.concat(res.data.result)
              this.pages = res.data.pages
            } else { // 该订单没有权益，隐藏
              this.hiddenRight = false
            }
          } else { // 该订单有权益，查询失败--提示
            this.finished = true
            this.noData = true
            // Toast('测试提醒')
            Toast(res.message)
          }
        }).catch((err) => {
          console.log(err)
        }).finally(() => {
          this.loading = false
        })
      } else {
        this.finished = true
      }
    },
    /**
     * @description  个人中心 订单详情
     * @param {cOrderId} 网单id number
     * @param {cOrderSn} 网单号 string
     * 返回接口请求数据
    */
    getDetail () {
      const context = this;
      const data = {
        cOrderId: context.data.cOrderId,
        cOrderSn: context.data.cOrderSn
      }
      personalApi.getOrderDetail(data).then(res => {
        const _resData = res.data;
        if (_resData.success) {
          context.data.resData = _resData.data || {};
          if (context.data.resData.orderStatus === 202) { // 已取消的订单不展示权益模块
            this.hiddenRight = false
          }
          // 初始化物流信息数据
          this.initTrackinfo()
        } else {
          // Toast('测试提醒')
          Toast.fail(_resData.message);
        }
      })
    },
    /**
     * @description 获取物流追踪信息
    */
    initTrackinfo() {
      const param = {
        orderSn: this.data.resData.orderSn, // 订单号 必传
        corderSn: this.data.resData.cOrderSn // 网单号
      }
      personalApi.getTrackFlowOrderDetail(param).then(res => {
        if (res.data.success) {
          this.trackInfo = res.data.data
        }
      })
    },
    /**
     * 顶部返回
    */
    doBack() {
    },

  },
}
</script>

